import axios from "axios";
import React, { Component } from "react";
import Loader from "react-loader-spinner";
import { withRouter } from "react-router";
import { NavLink } from "react-router-dom";
import Book from "../content/book/book";
import s from "./searchResult.module.css";

class SearchResult extends Component {
  state = {
    books: [],
    offset: 1,
    isFetching: true,
  };

  toTop(e) {
    window.scrollTo(null, 0);
    e.target.blur();
  }

  componentDidMount() {
    this.getBooks();
  }

  shouldComponentUpdate(nextProps, nextState) {
    return (
      this.props !== nextProps ||
      JSON.stringify(this.state) !== JSON.stringify(nextState)
    );
  }

  componentDidUpdate() {
    this.getBooks();
  }

  getBooks() {
    const tempBooks = [];
    axios
      .get(
        ` https://openlibrary.org/search.json?q=${
          this.props.match.params.query
        }&offset=${this.props.match.params.offset * 36 - 36}&limit=36`
      )
      .then((result) => result.data)
      .then((data) => {
        data.docs.forEach((item) => {
          tempBooks.push({
            title: item.title,
            imgUrl: `https://covers.openlibrary.org/b/id/${item.cover_i}-M.jpg`,
            bookId: item.key.match(/\d+/g).join(""),
          });
        });
        this.state.isFetching = false;
        this.setState({
          books: [...tempBooks],
          offset: this.props.match.params.offset,
        });
      })
      .catch();
  }

  render() {
    return (
      <>
        <div className="fs-5">Search results</div>
        {!this.state.isFetching && (
          <div className={s.content}>
            {this.state.books.map((item, index) => (
              <Book key={index} data={item} />
            ))}
          </div>
        )}
        {this.state.isFetching && (
          <Loader type="ThreeDots" color="rgb(130, 185, 0)" />
        )}
        <div className="d-flex justify-content-center">
          <nav aria-label="Page navigation example">
            <ul className="pagination">
              {this.state.offset != 1 && (
                <>
                  <li className="page-item">
                    <NavLink
                      onClick={this.toTop}
                      className="page-link"
                      to={`/search/${this.props.match.params.query}/${
                        +this.state.offset - 1
                      }`}
                      aria-label="Previous"
                    >
                      <span aria-hidden="true">&laquo;</span>
                    </NavLink>
                  </li>
                  <li className="page-item">
                    <NavLink
                      onClick={this.toTop}
                      to={`/search/${this.props.match.params.query}/${
                        +this.state.offset - 1
                      }`}
                      className="page-link"
                    >
                      {+this.state.offset - 1}
                    </NavLink>
                  </li>
                </>
              )}
              <li className="page-item active">
                <NavLink
                  onClick={this.toTop}
                  to={`/search/${this.props.match.params.query}/${+this.state
                    .offset}`}
                  className="page-link"
                >
                  {+this.state.offset}
                </NavLink>
              </li>
              <li className="page-item">
                <NavLink
                  onClick={this.toTop}
                  to={`/search/${this.props.match.params.query}/${
                    +this.state.offset + 1
                  }`}
                  href="#header"
                  className="page-link"
                >
                  {+this.state.offset + 1}
                </NavLink>
              </li>
              <li className="page-item">
                <NavLink
                  onClick={this.toTop}
                  to={`/search/${this.props.match.params.query}/${
                    +this.state.offset + 1
                  }`}
                  className="page-link"
                  aria-label="Next"
                >
                  <span aria-hidden="true">&raquo;</span>
                </NavLink>
              </li>
            </ul>
          </nav>
        </div>
      </>
    );
  }
}

export default withRouter(SearchResult);
